.App{
  min-height: 100vh;
  background-image: linear-gradient(45deg, #ff9a9e 0%, #fad0c4 99%, #fad0c4 100%);
}
.search-form{
  min-height: 10vh;
  display:flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width:50%;
  border: none;
  padding:10px;
}
.search-button{
  border-top: 10px;
  background:lightcoral;
  border:none;
  padding: 10px;
  color: white;
}

.recipes{
  display:flex;
  justify-content:face-around;
  flex-wrap: wrap;

}